import { OnInit, OnDestroy, forwardRef, Component } from '@angular/core';
import { ControlValueAccessor, FormControl, NG_VALUE_ACCESSOR } from '@angular/forms';
import { Subscription } from 'rxjs';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';

const CHECKBOX_CONTROL_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => CheckboxComponent),
  multi: true
};

@Component({
  selector: 'app-checkbox',
  templateUrl: './checkbox-item.model.html',
  providers: [CHECKBOX_CONTROL_VALUE_ACCESSOR]
})

export class CheckboxComponent implements OnInit, OnDestroy, ControlValueAccessor {
  public _onChangeCallback: (_: any) => {};
  public _onTouchedCallback: () => {};
  public control: FormControl = new FormControl();

  private subscription: Subscription;

  public writeValue(value: string): void {
    this.control.setValue(value);
  }

  public registerOnChange(fn: () => {}): void {
    this._onChangeCallback = fn;
  }

  public registerOnTouched(fn: () => {}): void {
    this._onTouchedCallback = fn;
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }
  ngOnInit(): void {
    this.initListener();
  }

  private initListener(): void {
    this.subscription = this.control.valueChanges.pipe(debounceTime(250),
      distinctUntilChanged()).subscribe(item => {
        this._onChangeCallback(item);
      });
  }
}
