import { Injectable } from '@angular/core';
import { HttpService } from './http-service.ts/http.service';
import { Observable, of } from 'rxjs';
// tslint:disable-next-line:max-line-length
import { ILogin, IRegister, IInitialRegisterData, IPassword, IPhoneConfirmation, IChangePassword, IEmail } from '../../../client/pages/account/models/account.model';
import { HttpHeaders } from '@angular/common/http';

@Injectable()
export class AccountApiService {
    constructor(private readonly http: HttpService) {
    }

    private urlPrefix = 'api/account/';

    public getAuthToken(model: ILogin): Observable<string> {

        return this.http.post<string>(`${this.urlPrefix}login`, model);
    }

    public register(model: IInitialRegisterData): Observable<string> {
        return this.http.post<string>(`${this.urlPrefix}register`, model);
    }

    public resendCode(model: IInitialRegisterData): Observable<string> {
        return this.http.post<string>(`${this.urlPrefix}resendPhoneConfirmation`, model);
    }

    public confirmPhone(model: IPhoneConfirmation): Observable<string> {
        return this.http.post<string>(`${this.urlPrefix}confirmPhone`, model);
    }

    public setPassword(model: IPassword): Observable<string> {
        return this.http.post<string>(`${this.urlPrefix}setPassword`, model);
    }

    public changePassword(model: IChangePassword): Observable<string> {
        return this.http.post<string>(`${this.urlPrefix}changePassword`, model);
    }

    public forgotPassword(email: IEmail): Observable<string> {
        return this.http.post<string>(`${this.urlPrefix}forgotPassword`, email);
    }
}
