import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Subject, Observable } from 'rxjs';
import { AuthTokenProvider } from '../../authentification/auth-token-provider';
import { IRequestOptions } from './request-options';
import { catchError } from 'rxjs/operators';
import { environment } from '../../../../../environments/environment.prod';
declare const gApiBaseUrl;

@Injectable()
export class HttpService {
    public logoutMessageStream = new Subject<void>();
    private baseUrl: string;

    constructor(private httpClient: HttpClient,
        private router: Router,
        private authTokenProvider: AuthTokenProvider) {
        const baseUrl = environment.production ? 'http://kordamentha3467.cloudapp.net:8081' : 'http://localhost:58412';
        this.setBaseUrl(baseUrl);
    }

    public request<T>(method: string, url: string, body: any, options?: IRequestOptions): Observable<T> {
        options = this.IntitializeOptions(options, body);
        this.setAuthHeader(options);
        url = this.applyBaseUrl(url);

        return this.httpClient.request<T>(method, url, options).pipe(catchError(this.catchAuthError()));
    }

    public get<T>(url: string, options?: IRequestOptions): Observable<T> {
        return this.request('get', url, null, options);
    }

    public post<T>(url: string, body: any, options?: IRequestOptions): Observable<T> {
        return this.request('post', url, body, options);
    }

    public put<T>(url: string, body: any, options?: IRequestOptions): Observable<T> {
        return this.request('put', url, body, options);
    }

    public delete<T>(url: string, options?: IRequestOptions): Observable<T> {
        return this.request('delete', url, options);
    }

    public setBaseUrl(baseUrl: string) {
        this.baseUrl = baseUrl.endsWith('/') ? baseUrl : baseUrl + '/';
    }

    private catchAuthError() {
        return (response: Response) => {
            if (response.status === 401) {
                this.logoutMessageStream.next();
                this.router.navigate(['signIn']);
            }
            return Observable.throw(response);
        };
    }

    private setAuthHeader(options?: IRequestOptions): void {
        const token: string = this.authTokenProvider.getToken();
        options.headers = options.headers.set('Authorization', `Bearer ${token}`);
    }

    private IntitializeOptions(options: IRequestOptions, body: any): IRequestOptions {
        if (!options) {
            options = { headers: new HttpHeaders() };
        }
        if (!options.headers) {
            options.headers = new HttpHeaders();
        }

        options.headers = options.headers.set('Content-Type', `Application/json`);

        options.body = body;

        return options;
    }

    private applyBaseUrl(url: string): string {
        url = url.startsWith('/') ? url.slice(1) : url;
        return this.baseUrl + url;
    }
}
