import { Component, OnInit, Input } from '@angular/core';
import { AccountApiService } from '../../../../core/services/api/account.api.service';
import { AuthenticationService } from '../../../../core/services/authentification/authentication.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, Validators, FormControl, ValidationErrors } from '@angular/forms';
import { GlobalSpinnerService } from '../../../../core/services/shared/global-spinner.service';
import { IPassword, PageComponent, IRegister, IChangePassword } from '../models/account.model';
import { finalize } from 'rxjs/operators';
import { passwordValidator } from '../../../../utils/validatorBuilder';

@Component({
    selector: 'app-resetpassword',
    templateUrl: './resetPassword.component.html'
})
export class ResetPasswordComponent implements OnInit {
    public passwordForm: FormGroup;
    public passwordModel: IPassword;
    public changePasswordToken = '';
    public email = '';

    constructor(private accountApiService: AccountApiService,
        private authenticationService: AuthenticationService,
        private formBuilder: FormBuilder,
        private router: Router,
        private route: ActivatedRoute,
        private spinnerService: GlobalSpinnerService) {
    }

    public ngOnInit(): void {
        this.initPasswordForm();
        this.route
            .queryParams
            .subscribe(params => {
                this.changePasswordToken = params['changePasswordToken'];
                this.email = params['email'];
            });
    }

    public signIn() {
        this.spinnerService.show();
        const model = this.fillForgotPasswordModel();
        if (this.isPasswordsMatch(model)) {
            this.accountApiService.changePassword(model)
                .pipe(finalize((() => this.spinnerService.hide())))
                .subscribe((authToken: string) => {
                    this.authenticationService.logIn(authToken);
                    this.router.navigate(['home']);
                }, () => {
                    // this.notificationService.error('Error', 'Error during registration');
                });
        }
        this.router.navigate(['signIn']);
    }

    public initPasswordForm(): void {
        this.passwordForm = this.formBuilder.group({
            password: ['', [
                Validators.required,
                this.passwordValidator]
            ],
            confirmPassword: ['', [
                Validators.required,
                this.isPasswordsMatch]
            ],
        });
    }

    public fillForgotPasswordModel(): IChangePassword {
        const model: IChangePassword = {
            email: this.email,
            changePasswordToken: this.changePasswordToken,
            ...this.passwordForm.value
        };
        return model;
    }

    public isPasswordsMatch({ password, confirmPassword }): boolean {
        return password === confirmPassword;
    }

    public passwordValidator(control: FormControl): ValidationErrors {
        return passwordValidator(control);
    }

    public cancel() {
        this.router.navigate(['signIn']);
    }
}
