import { Component } from '@angular/core';
import { GlobalSpinnerService } from '../../services/shared/global-spinner.service';

@Component({
    selector: 'app-global-spinner',
    templateUrl: 'global-spinner.component.html',
})
export class GlobalSpinnerComponent {
    constructor(private siteSpinnerService: GlobalSpinnerService) {
        siteSpinnerService.spinnerStateStream.subscribe((state) => this.isLoading = state);
        siteSpinnerService.spinnerTextStream.subscribe((text) => this.loadingText = text);
    }

    public isLoading = false;
    public loadingText = '';
}
