import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService } from '../../../core/services/authentification/authentication.service';
import { User } from '../account/models/account.model';

@Component({
    selector: 'app-sidebar-component',
    templateUrl: './sidebar.component.html'
})
export class SidebarComponent implements OnInit {
    public isLoggedIn: boolean;
    public user: User;

    constructor(
        private authenticationService: AuthenticationService,
        private router: Router) {
    }

    ngOnInit() {
        this.authenticationService.authenticatedUserSource.subscribe((user: User) => {
            this.isLoggedIn = this.authenticationService.isLoggedIn;
            this.user = user;
        }
    );
}

    public goToRegisterPage() {
        this.router.navigate(['register']);
    }

    public goToSignInPage(): void {
        this.authenticationService.logOut();
        this.router.navigate(['signIn']);
    }
}
