import { Component, OnInit, Input } from '@angular/core';
import { PageComponent, IInitialRegisterData } from '../models/account.model';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { AuthenticationService } from '../../../../core/services/authentification/authentication.service';
import { AccountApiService } from '../../../../core/services/api/account.api.service';
import { Router } from '@angular/router';
import { GlobalSpinnerService } from '../../../../core/services/shared/global-spinner.service';
import { phoneMask, passwordValidator, emailValidator } from '../../../../utils/validatorBuilder';
import { finalize } from 'rxjs/operators';
import { MatDialog } from '@angular/material';
import { AlertComponent } from '../../../../core/components/dialog/alert/alert.component';
import { RegistrationStateService } from './registrationState.service';
import { PrivacyPoliticsComponent } from '../../../../core/components/dialog/privacy-politics/privacy-politics.component';

@Component({
    selector: 'app-registration',
    templateUrl: './registration.component.html',
    styleUrls: ['../signIn/signIn.component.scss']
})

export class RegistrationComponent implements OnInit, PageComponent {
    @Input() data: any;
    public initialForm: FormGroup;
    public phoneMask = phoneMask;
    public isChosen = new FormControl(false);

    constructor(private accountApiService: AccountApiService,
        private authenticationService: AuthenticationService,
        private formBuilder: FormBuilder,
        private router: Router,
        private spinnerService: GlobalSpinnerService,
        private reistrationStateService: RegistrationStateService,
        private dialog: MatDialog,
        private pageStateService: RegistrationStateService) {
    }

    public ngOnInit(): void {
        this.initForm();
    }

    public initForm(): void {
        this.initialForm = this.formBuilder.group({
            email: ['', [Validators.required, emailValidator]],
            phone: ['', [Validators.required]]
        });
    }

    public fillCommonModel(): IInitialRegisterData {
        this.data = this.initialForm.value;
        return this.initialForm.value;
    }

    public isControlInvalid(controlName: string): boolean {
        const control = this.initialForm.controls[controlName];
        return control.invalid && control.touched;
    }

    public noneSelected() {
        return (!this.isChosen.value || this.initialForm.invalid);

    }

    public showVerificationScreen() {
        const dialogRef = this.dialog.open(AlertComponent, {
            autoFocus: false,
            data: {
                title: 'Check your phone!',
                message: 'Verification code was recent.'
            },
            panelClass: 'dialog-container'
        });
        dialogRef.afterClosed().subscribe(() => {
            this.spinnerService.show();
            const controls = this.initialForm.controls;
            if (this.initialForm.invalid) {
                Object.keys(controls)
                    .forEach(controlName => controls[controlName].markAsTouched());
                return;
            }
            this.accountApiService.register(this.fillCommonModel())
                .pipe(finalize(() => this.spinnerService.hide()))
                .subscribe((code: string) => {
                    this.pageStateService.nextStep(1, this.data);
                },
                    () => {
                        // this.notificationService.error('Error', 'Error logging in');
                    });
        });
    }

    public cancel() {
        this.router.navigate(['home']);
    }

    public termsOfUse(): void {
        const dialogRef = this.dialog.open(PrivacyPoliticsComponent, {
            autoFocus: false,
            data: {
                title: 'Personal data politics',
                // tslint:disable-next-line:max-line-length
                content: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry\'s standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book.  All the Lorem Ipsum generators on the Internet tend to repeat predefined chunks as necessary.Many desktop publishing packages and web page editors now use Lorem Ipsum as their default model text, and a search for \'lorem ipsum\' will uncover many web sites.'
            },
            panelClass: 'privacy-politics-dialog-container'
        });
        dialogRef.afterClosed().subscribe(result => {
            this.isChosen.setValue(result);
        });
    }
}
