import { Component, Input, OnInit } from '@angular/core';
import { AccountApiService } from '../../../../core/services/api/account.api.service';
import { GlobalSpinnerService } from '../../../../core/services/shared/global-spinner.service';
import { FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { IEmail } from '../models/account.model';

@Component({
    selector: 'app-emailprovider',
    templateUrl: './provideEmail.component.html',
    styleUrls: ['../signIn/signIn.component.scss']
})
export class ProvideEmailComponent {
    public email = new FormControl('', [Validators.required, Validators.email]);

    constructor(private accountApiService: AccountApiService,
                private router: Router,
                private spinnerService: GlobalSpinnerService) {
    }

    public resetPassword() {
        const model: IEmail = {
            email: this.email.value,
        };

        this.accountApiService.forgotPassword(model)
            .subscribe((changePasswordToken: any) => {
                // TODO: This is temporary code. User will get an email with link to resetPassword page
                this.router.navigate(['resetPassword'],
                { queryParams: { email: this.email.value, changePasswordToken: changePasswordToken.token } });
            },
                () => {
                    // this.notificationService.error('Error', 'Error logging in');
                });
    }

    public isControlInvalid(): boolean {
        return this.email.invalid && this.email.touched;
    }

    public cancel() {
        this.router.navigate(['home/signIn']);
    }
}
