import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable()
export class GlobalSpinnerService {

    public spinnerStateStream: Subject<boolean> = new Subject<boolean>();
    public spinnerTextStream: Subject<string> = new Subject<string>();

    public show(text: string = ''): void {
        this.spinnerStateStream.next(true);
        this.spinnerTextStream.next(text);
    }

    public hide(): void {
        this.spinnerStateStream.next(false);
    }
}
