import { Injectable } from '@angular/core';
import { RegistrationComponent } from './registration.component';
import { VerificationComponent } from './verification.component';
import { RegistrationPasswordComponent } from './password.component';
import { RegistrationItem } from '../models/account.model';
import { Subject, Observable, BehaviorSubject } from 'rxjs';

@Injectable()
export class PageService {
  getItems() {
    return [
      new RegistrationItem(RegistrationComponent, null),

      new RegistrationItem(VerificationComponent, null),

      new RegistrationItem(RegistrationPasswordComponent, null)
    ];
  }
}
