import { Component, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

@Component({
    // tslint:disable-next-line:component-selector
    selector: 'privacy-politics',
    styleUrls: ['./privacy-politics.component.scss'],
    templateUrl: './privacy-politics.component.html'
})
export class PrivacyPoliticsComponent {
    constructor(public dialogRef: MatDialogRef<PrivacyPoliticsComponent>,
                @Inject(MAT_DIALOG_DATA) public data: any) { }

    public close(): void {
        this.dialogRef.close(false);
    }

    public aprouve(): void {
        this.dialogRef.close(true);
    }
}
