import { Component, Input, OnInit, ViewChild, ComponentFactoryResolver, ComponentRef } from '@angular/core';
import { RegistrationItem, PageComponent } from '../models/account.model';
import { PageService } from './page.service';
import { isNullOrUndefined } from 'util';
import { RegistrationStateService } from './registrationState.service';
import { PageDirective } from '../../../../core/services/shared/page.directive';

@Component({
  selector: 'app-container.component',
  templateUrl: './container.component.html'
})

export class PageResolverComponent implements OnInit {
  @ViewChild(PageDirective) adHost: PageDirective;
  items: RegistrationItem[];

  constructor(private componentFactoryResolver: ComponentFactoryResolver,
    private pageService: PageService,
    private reistrationStateService: RegistrationStateService) { }

  ngOnInit() {
    this.items = this.pageService.getItems();
    this.reistrationStateService.step.subscribe(({ index, data }) => {
      this.loadComponent(index, data);
    });
  }

  loadComponent(componentIndex?: number, data?: any) {
    if (isNullOrUndefined(componentIndex)) {
      componentIndex = 0;
    }
    const item = this.items[componentIndex];

    const componentFactory = this.componentFactoryResolver.resolveComponentFactory(item.component);

    const viewContainerRef = this.adHost.viewContainerRef;
    viewContainerRef.clear();

    const componentRef = viewContainerRef.createComponent(componentFactory);
    (<PageComponent>componentRef.instance).data = data;
    componentRef.changeDetectorRef.detectChanges();
  }
}
