import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { SidebarComponent } from './client/pages/sidebar/sidebar.component';
import { Authorize } from './core/services/authentification/authorize';
import { SignInComponent } from './client/pages/account/signIn/signIn.component';
import { RegistrationComponent } from './client/pages/account/registration/registration.component';
import { PageResolverComponent } from './client/pages/account/registration/container.component';
import { ResetPasswordComponent } from './client/pages/account/changePassword/resetPassword.component';
import { ProvideEmailComponent } from './client/pages/account/changePassword/provideEmail.component';
import { HomeComponent } from './client/pages/home/home.component';
import { WelcomeComponent } from './client/pages/welcome/welcome.component';

const routes: Routes = [
    {
        path: '',
        pathMatch: 'full',
        redirectTo: '/home'
    },
    {
        path: 'home',
        component: HomeComponent,
        children: [
            {
                path: '',
                component: WelcomeComponent
            },
            {
                path: 'signIn',
                component: SignInComponent
            },
            {
                path: 'register',
                component: PageResolverComponent
            }
        ]
    },
    {
        path: 'provideEmail',
        component: ProvideEmailComponent
    },
    {
        path: 'resetPassword',
        component: ResetPasswordComponent
    }
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRouterModule { }
