import { Injectable } from '@angular/core';
import { UserApiService } from '../api/user.api.service';
import { GlobalSpinnerService } from '../shared/global-spinner.service';
import { AuthTokenProvider } from './auth-token-provider';
import { BehaviorSubject } from 'rxjs';
import { User } from '../../../client/pages/account/models/account.model';
import { HttpService } from '../api/http-service.ts/http.service';
import { finalize } from 'rxjs/operators';

@Injectable()
export class AuthenticationService {
    public authenticatedUserSource: BehaviorSubject<User>;

    private user: User;
    private _isLoggedIn: boolean;

    constructor(private userApiService: UserApiService,
                private spinnerService: GlobalSpinnerService,
                private authTokenProvider: AuthTokenProvider,
                private httpService: HttpService) {
        this.authenticatedUserSource = new BehaviorSubject<User>(null);
        this.httpService.logoutMessageStream.subscribe(() => {
            this.logOut();
        });

        const token = authTokenProvider.getToken();
        if (this.authTokenProvider.getToken()) {
            this.logIn(token);
        }
    }

    public get isLoggedIn(): boolean {
        return this._isLoggedIn;
    }

    public get authenticatedUser(): User {
        return this.user;
    }

    public updateAuthenticatedUserInfo(user: User): void {
        this.user = user;
        this.authenticatedUserSource.next(this.user);
    }

    public logIn(token: string): void {
        this._isLoggedIn = true;
        this.authTokenProvider.setToken(token);

        // this.loadUser();
    }

    public logOut(): void {
        this._isLoggedIn = false;
        this.updateAuthenticatedUserInfo(null);
        this.authTokenProvider.deleteToken();

    }

    private loadUser(): void {
        this.spinnerService.show();

        this.userApiService.getAuthenticatedUserDetails()
            .pipe(finalize((() => this.spinnerService.hide())))
            .subscribe((user: User) => {
                this.updateAuthenticatedUserInfo(user);
            });
    }
}
