import { Component, ChangeDetectionStrategy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService } from '../../../core/services/authentification/authentication.service';
import { User } from '../account/models/account.model';

@Component({
    selector: 'app-welcome',
    templateUrl: './welcome.component.html',
    styleUrls: ['./welcome.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})

export class WelcomeComponent implements OnInit {
    public isLoggedIn: boolean;
    public user: User;

    constructor(
        private authenticationService: AuthenticationService,
        private router: Router) { }

    ngOnInit() {
        this.authenticationService.authenticatedUserSource.subscribe((user: User) => {
            this.isLoggedIn = this.authenticationService.isLoggedIn;
            this.user = user;
        });
    }
}
