import { Injectable } from '@angular/core';
import { HttpService } from './http-service.ts/http.service';
import { Observable } from 'rxjs';
import { User, IChangePassword } from '../../../client/pages/account/models/account.model';

@Injectable()
export class UserApiService {
    constructor(private readonly http: HttpService) {}

    private urlPrefix = 'user/';

    public getDetails(userId: string): Observable<User> {
        return this.http.get<User>(`${this.urlPrefix}getDetails/?userId=${userId}`);
    }

    public getAuthenticatedUserDetails(): Observable<User> {
        return this.http.get<User>(`${this.urlPrefix}getAuthenticatedUserDetails`);
    }

    public updateUserInfo(model: User): Observable<User> {
        return this.http.post<User>(`${this.urlPrefix}update`, model);
    }

    public changePassword(model: IChangePassword): Observable<string> {
        return this.http.post<string>(`${this.urlPrefix}changePassword`, model);
    }
}
