import {
    Component,
    forwardRef,
    Input,
    EventEmitter,
    Output,
    ViewChild,
    ElementRef,
    OnInit,
    OnDestroy
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR, FormControl } from '@angular/forms';
import { TIME_FORMAT_DTO, DATE_FORMAT_EDIT, DATE_RANGE_MIN, DATE_RANGE_MAX } from '../constants/date.constants';
import { IDropdownItem } from '../models/dropdown-item.model';
import { InputValue } from './input.model';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { Subscription } from 'rxjs';

interface Types {
    text: boolean;
    longText: boolean;
    select: boolean;
    date: boolean;
    time: boolean;
    numeric: boolean;
}

const INPUT_CONTROL_VALUE_ACCESSOR: any = {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => InputComponent),
    multi: true
};

@Component({
    selector: 'app-input',
    templateUrl: './input.component.html',
    providers: [INPUT_CONTROL_VALUE_ACCESSOR]
})
export class InputComponent implements OnInit, OnDestroy, ControlValueAccessor {

    // a comma separated list of input elements to render
    @Input() public controlType: string;
    @Input() public itemList: Array<IDropdownItem>;
    @Input() public max?: Date;
    @Input() public min?: Date;
    @Input() public disabled?: boolean;
    @Input() public value: InputValue;

    public control: FormControl = new FormControl();
    public minDate: Date = DATE_RANGE_MIN;
    public maxDate: Date = DATE_RANGE_MAX;
    public types: Types = {
        text: false,
        longText: false,
        select: false,
        date: false,
        time: false,
        numeric: false
    };
    public _onChangeCallback: (_: any) => {};
    public _onTouchedCallback: () => {};
    private subscription: Subscription;

    public ngOnInit(): void {
        this.setType();
        this.initListener();
    }

    public ngOnDestroy(): void {
        this.subscription.unsubscribe();
    }

    public setType(): void {
        this.types[this.controlType] = true;
    }

    public writeValue(value: string): void {
        this.control.setValue(value);
    }

    public registerOnChange(fn: () => {}): void {
        this._onChangeCallback = fn;
    }

    public registerOnTouched(fn: () => {}): void {
        this._onTouchedCallback = fn;
    }

    private initListener(): void {
        this.subscription = this.control.valueChanges.pipe(debounceTime(250),
            distinctUntilChanged()).subscribe(item => {
                this._onChangeCallback(item);
            });
    }
}
