import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ILogin, User } from '../models/account.model';
import { Router } from '@angular/router';
import { AccountApiService } from '../../../../core/services/api/account.api.service';
import { AuthenticationService } from '../../../../core/services/authentification/authentication.service';
import { GlobalSpinnerService } from '../../../../core/services/shared/global-spinner.service';
import { finalize } from 'rxjs/operators';
import { PageService } from '../registration/page.service';
import { AlertComponent } from '../../../../core/components/dialog/alert/alert.component';
import { MatDialog } from '@angular/material';
import { ArrayType } from '@angular/compiler/src/output/output_ast';
import { emailValidator, passwordValidator } from 'src/app/utils/validatorBuilder';


@Component({
    selector: 'app-signin',
    templateUrl: './signIn.component.html',
    styleUrls: ['./signIn.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})

export class SignInComponent implements OnInit {
    public createForm: FormGroup;
    public errorMessage: string;

    constructor(private accountApiService: AccountApiService,
                private authenticationService: AuthenticationService,
                private formBuilder: FormBuilder,
                private router: Router,
                private spinnerService: GlobalSpinnerService,
                private dialog: MatDialog) { }

    public ngOnInit(): void {
        this.initForm();
    }

    public login(): void {
        this.spinnerService.show();
        this.accountApiService.getAuthToken(this.fillModel())
            .pipe(finalize(() => this.spinnerService.hide()))
            .subscribe((authToken: any) => {
                this.authenticationService.logIn(authToken.token);
                this.router.navigate(['home']);
            },
                () => {
                    // this.notificationService.error('Error', 'Error logging in');
                });
    }

    public initForm(): void {
        this.createForm = this.formBuilder.group({
            email: ['', [Validators.required, emailValidator]],
            password: ['', [Validators.required, passwordValidator]],
            rememberMe: ''
        });
    }

    public isControlInvalid(controlName: string): boolean {
        const control = this.createForm.controls[controlName];
        return control.invalid && control.touched;
    }

    public fillModel(): ILogin {
        return this.createForm.value;
    }

    public cancel() {
        this.router.navigate(['home']);
    }
}
