import { Component, Input } from '@angular/core';
import { AccountApiService } from '../../../../core/services/api/account.api.service';
import { AuthenticationService } from '../../../../core/services/authentification/authentication.service';
import { FormBuilder, FormGroup, FormControl } from '@angular/forms';
import { GlobalSpinnerService } from '../../../../core/services/shared/global-spinner.service';
import { PageComponent, IVerificationCode, IPhoneConfirmation } from '../models/account.model';
import { finalize } from 'rxjs/operators';
import { RegistrationStateService } from './registrationState.service';

@Component({
    selector: 'app-verification',
    templateUrl: './verification.component.html',
    styleUrls: ['../signIn/signIn.component.scss']
})

export class VerificationComponent implements PageComponent {
    @Input() data: any;
    public verificationCode = new FormControl('');

    constructor(private accountApiService: AccountApiService,
        private authenticationService: AuthenticationService,
        private formBuilder: FormBuilder,
        private spinnerService: GlobalSpinnerService,
        private registrationStateService: RegistrationStateService) {
    }

    public resendCode() {
        this.spinnerService.show();
        this.accountApiService.resendCode(this.data)
            .pipe(finalize(() => this.spinnerService.hide()))
            .subscribe((code: string) => {
            },
                () => {
                    // this.notificationService.error('Error', 'Error logging in');
                });
    }

    public verificate() {
        const model: IPhoneConfirmation = {
            email: this.data.email,
            phone: this.data.phone,
            code: this.verificationCode.value
        };

        this.spinnerService.show();
        this.accountApiService.confirmPhone(model)
            .pipe(finalize(() => this.spinnerService.hide()))
            .subscribe((code: string) => {
                this.registrationStateService.nextStep(2, { model, code });
            },
                () => {
                    // this.notificationService.error('Error', 'Error logging in');
                });
    }

    public goBack() {
        this.registrationStateService.nextStep(0, null);
    }
}
