import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable()
export class RegistrationStateService {
    public currentStep = new BehaviorSubject({ index: 0, data: null });

    public get step(): Observable<any> {
        return this.currentStep.asObservable();
    }
    nextStep(index: number, data: any) {
        this.currentStep.next({ index, data });
    }
}
